import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { UserOptions } from '../../interfaces/user-options';
import { NgForm } from '@angular/forms';
import { Http, Headers, RequestOptions } from '@angular/http';
import { AlertController } from 'ionic-angular';
import { LoginPage } from '../login/login';
/**
 * Generated class for the ForgotpasswordPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

let headers = new Headers({ 'Content-Type': 'application/json' });
let options = new RequestOptions({ headers: headers });

@IonicPage()
@Component({
  selector: 'page-user',
  templateUrl: 'forgotpassword.html',
})
export class ForgotpasswordPage {
  
  url: String = 'auth/frgtpwd/';
  login: UserOptions = { username: '', password: '' };
  submitted: boolean = false;

  constructor(public navCtrl: NavController, public http: Http, 
    public alrtCtrl: AlertController,
    public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ForgotpasswordPage');
  }

  onForgotPassword(form: NgForm){
    if(form.valid){
        var mobileNo = this.login.username;
        this.http.get(this.url + mobileNo, options).map(res => res.json()).subscribe(data => {
            if(data.statusCode == '00' && data.responseBean != undefined){
              if(data.responseBean.enabled == "true"){
                let alert = this.alrtCtrl.create({
                  title: 'Alert!',
                  subTitle: 'Your password is sent to your registered email address.',
                  buttons: 
                  [
                    {
                      text: 'Back to login',
                        handler: () => {
                          this.navCtrl.push(LoginPage);
                        }
                    }
                ]
                });
                alert.present();
                // let prompt = this.alrtCtrl.create({
                //   title: 'Enter pin!',
                //   message: "A security pin is sent to your registered email address. Enter the pin",
                //   inputs: [
                //     {
                //       name: 'pin',
                //       placeholder: 'pin'
                //     },
                //   ],
                //   buttons: [
                //     {
                //       text: 'Cancel',
                //       handler: data => {
                //         console.log('Cancel clicked');
                //       }
                //     },
                //     {
                //       text: 'Submit',
                //       handler: data => {
                //         console.log('Saved clicked');
                //       }
                //     }
                //   ]
                // });
                // prompt.present();
                //this.navCtrl.push();    // todo: navigate to reset password page
              }
            }
            else{
              let alert = this.alrtCtrl.create({
                title: 'Error!',
                subTitle: 'The entered mobile number is not found!',
                buttons: ['OK']
              });
              alert.present();
            }
        });
    }
  }

}



