import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';

import { NavController,MenuController,LoadingController, AlertController, IonicPage } from 'ionic-angular';
import { PasswordChange } from '../../interfaces/password-change';
import {Http,Headers, RequestOptions} from '@angular/http';
import { LoginPage } from '../login/login'

  let headers = new Headers({ 'Content-Type': 'application/json' });
  let options = new RequestOptions({ headers: headers });

  let apiUrl="auth/changepwd";


@IonicPage()
@Component({
  selector: 'page-password-change',
  templateUrl: 'password-change.html'

})
export class PasswordChangePage {

    chgpwd: PasswordChange = { oldpin: '', newpin: '',retypepin:'' };
    submitted = false;
    profile:any=[];
    username:String;

    constructor(
      public navCtrl: NavController,
      private menu:MenuController,
      public loadingCtrl: LoadingController,
      private http: Http,
      private alertCtrl: AlertController
      ){
      this.username = JSON.parse(localStorage.getItem('profile'));
          console.log(this.username);

       }

     ionViewDidEnter() {
      this.menu.swipeEnable(false);
    }

    ionViewWillLeave() {
      this.menu.swipeEnable(true);
    }

    onChgpwd(form: NgForm) {

      const loading = this.loadingCtrl.create({
        spinner: 'bubbles',
        content: "Please Wait..."

      });


      this.submitted = true;

      if (form.valid) {

        let postdata={


          oldPassword:this.chgpwd.oldpin,
          newPassword:this.chgpwd.newpin,
          retypePassword:this.chgpwd.retypepin


        };

        console.log(postdata);

        loading.present();

        this.http.put(apiUrl+'/'+ this.username,postdata,options).map(res => res.json())
          .subscribe(data => {
           console.log(data);
                if(data.statusCode=="00"){
                 
                  loading.dismiss();

                  let alertct = this.alertCtrl.create({
                                      title: 'Success!',
                                      subTitle: 'Your Password has been Changed  ',
                                      buttons:['OK']
                                    });
                  alertct.present();
                  this.navCtrl.push(LoginPage);

                }
                else if(data.statusCode=="99"){
                 
                  loading.dismiss();

                  let alertct = this.alertCtrl.create({
                                      title: 'Fail!',
                                      subTitle: 'Check Enter Password ',
                                      buttons:['OK']
                                    });
                  alertct.present();
               
                }

                else{
                  alert(data.error);
                  loading.dismiss();
                }

              },
              err => {
                if(err){
                loading.dismiss();
                }
                console.log("ERROR!: ", err);
              }
          );


      }


    }


  }



