import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';

import { NavController,MenuController,LoadingController, AlertController } from 'ionic-angular';

import { UserRegistration } from '../../interfaces/user-registration';

import {Http,Headers, RequestOptions} from '@angular/http';

import { SignupSuccessfulPage } from '../signup-successful/signup-successful';

let headers = new Headers({ 'Content-Type': 'application/json' });
let options = new RequestOptions({ headers: headers });

let detailsUrl="register/profile/";
let registerUrl = "register/login/";

@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html'
})
export class SignupPage {
  signup: UserRegistration = { mobile: '', fullname: '',passport:'',lodge:'',pin:'' };
  submitted = false;

  constructor(
    public navCtrl: NavController, 
    private menu:MenuController,
    public loadingCtrl: LoadingController,
    private http: Http,
    private alertCtrl: AlertController
    ){}

   ionViewDidEnter() {
    this.menu.swipeEnable(false);
  }

  ionViewWillLeave() {
    this.menu.swipeEnable(true);
  }

  onMobileNoEntered(){
    if(this.signup.mobile.length == 0){
      return;
    }
    const loading = this.loadingCtrl.create({
      spinner: 'bubbles',
      content: "retrieving details..."
    });

    loading.present();
    this.http.get(detailsUrl + this.signup.mobile, options).map(res => res.json()).subscribe(data => {
      if(data.statusCode == '00' && data.responseBean != undefined){
        this.signup.fullname = data.responseBean.fullName;
        this.signup.passport = data.responseBean.ic_passport;
        if(data.responseBean.lodgePositionNames != undefined && data.responseBean.lodgePositionNames.length > 0){
          this.signup.lodge = data.responseBean.lodgePositionNames[0].lodgeName;
        }
        loading.dismiss();
        //  let alert = this.alertCtrl.create({
        //             title: 'Error!',
        //             subTitle: 'Mobile Number is Already Existed ',
        //             buttons:['OK']
        //           });

       if (this.signup.passport == undefined || this.signup.fullname == undefined 
        || this.signup.lodge == undefined){
        // alert.present();
        this.submitted = false;
        }
      }
      else{
        loading.dismiss();
          let alert = this.alertCtrl.create({
            title: 'Error!',
            subTitle: 'The entered mobile number is not found',
            buttons:['OK']
          });
          alert.present();
      }
    });
  }

  onSignup(form: NgForm) {
    
    const loading = this.loadingCtrl.create({
      spinner: 'bubbles',
      content: "Please Wait..."
    });


    this.submitted = true;

    if (form.valid) {

      let postdata={
        
        hashPwd:this.signup.pin
        
      };

      console.log(postdata);

      loading.present();

      this.http.post(registerUrl + this.signup.mobile, postdata, options).map(res => res.json())
        .subscribe(data => {
              if(data.statusCode == '00' && data.responseBean != undefined){
                loading.dismiss();
                this.navCtrl.push(SignupSuccessfulPage,{fullname:this.signup.fullname});
              }
              else{
                loading.dismiss();
                let alert = this.alertCtrl.create({
                  title: 'Error!',
                  subTitle: 'There was an error occurred while registering',
                  buttons:['OK']
                });
                alert.present();
              }
            },
            err => {
              if(err){
              loading.dismiss(); 
              }
              console.log("ERROR!: ", err);
            }
        );
     
      
    }


  }


}



